.box_folder,
.title_sub_new {
   padding: 0 15px;

   .title_box_category {
      padding: 0;

      h2 {
         font: bold px_to_rem(26px) $fontmain2;
         padding-right: 15px;
      }

      @media (max-width: 340px) {
         h2 {
            font: bold px_to_rem(24px) $fontmain2;
         }
      }

      &:before {
         content: "";
         position: absolute;
         right: 0;
         height: 3px;
         bottom: 8px;
         background-image: -moz-linear-gradient(left, #0799ff #9dfeaf);
         background-image: -webkit-gradient(linear, left top, right top, from(#16d1f2), to(#9dfeaf));
         background-image: -webkit-linear-gradient(left, #0799ff, #9dfeaf);
         background-image: -o-linear-gradient(left, #0799ff, #9dfeaf);
      }
   }

   &.line2 {
      .title_box_category {
         &:before {
            background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
            background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
            background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
            background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
         }
      }
   }

   &.line3 {
      .title_box_category {
         &:before {
            background-image: -moz-linear-gradient(left, #8419fd #57b2fb);
            background-image: -webkit-gradient(linear, left top, right top, from(#8419fd), to(#57b2fb));
            background-image: -webkit-linear-gradient(left, #8419fd, #57b2fb);
            background-image: -o-linear-gradient(left, #8419fd, #57b2fb);
         }
      }
   }
}

.title_sub_new {
   margin-bottom: 10px;

   .title_box_category {
      h2 {
         font: bold px_to_rem(26px) $fontmain
      }
   }
}

.box_folder {
   .art_item {
      margin-bottom: 15px;
      padding-bottom: 15px;
      border-bottom: 1px solid #eaeaea;

      .content {
         margin-top: 0;

         .title_news {
            font: 400 px_to_rem(18px) $fontmain;
            margin-bottom: 0;
            line-height: 1.4 !important;
         }
      }

      &.img {
         border-bottom: 0;

         .thumb_art {
            background: #eee;
            padding: 13px;
            width: 100%;
            margin: 0;
            float: none;
         }

         .content {
            margin-top: 10px;

            .title_news {
               font: bold px_to_rem(20px) $fontmain;
            }
         }

         &:before {
            content: "";
            background: #eee;
            position: absolute;
            width: 150px;
            height: 6px;
            bottom: -3px;
         }
      }

      &:last-child {
         border-bottom: 0;
         padding-bottom: 0;
         margin-bottom: 0;
      }
   }

   .taitro {
      margin-top: 10px;

      a {
         color: #999;
         font: 400 12px arial
      }
   }
}

/*box_taitro*/
.box_taitro {
   .art_item {
      .thumb_art {
         margin: 0 15px 0 0;
         float: left;
         width: 85px;
      }

      .content {
         margin-top: 0;

         .title_news {
            font: 400 px_to_rem(16px) $fontmain;
         }
      }
   }

   &_2 {
      background: #f0f0f0;
      padding: 10px 15px;
      margin: 0 15px 20px 15px;
      width: calc(100% - 30px);

      .title_box_category {
         margin-bottom: 30px;

         &:before {
            bottom: inherit;
            top: 0;
            height: 3px;
            background: none;
            left: 0;
         }

         &:after {
            content: "";
            background: #dfdfdf;
            height: 7px;
            width: 160px;
            position: absolute;
            bottom: -20px;
            left: 0;
         }

         h2 {
            background: none;
            padding-top: 15px;
            font: 500 px_to_rem(20px) $fontmain;
         }
      }

      .art_item {
         border-bottom: 1px solid #dfdfdf;
      }

      .art_item.img {
         border-bottom: 1px solid #dfdfdf;
         padding-bottom: 15px;
         margin-bottom: 15px;

         &:before {
            display: none;
         }

         .thumb_art {
            padding: 0 !important
         }
      }
   }
}

/*box_thuongmai*/
.box_thuongmai {
   .title_box_category {
      margin: 10px 0 20px 0;

      &:before {
         display: none;
      }
   }

   .owl-nav {
      &>div {
         position: absolute;
         top: -55px;
         width: 38px;
         height: 38px;
         line-height: 32px;
         text-align: center;
         border: 2px solid rgba(12, 164, 254, 0.75);
         border-radius: 4px;
         -webkit-border-radius: 4px;
         -moz-border-radius: 4px;

         .icon-ns {
            color: #0ca4fe;
            margin: 0;
         }

         &:hover {
            border: 0;
            line-height: 37px;
            background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
            background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
            background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
            background-image: -o-linear-gradient(left, #0799ff, #21d4fd);

            .icon-ns {
               color: #fff
            }
         }
      }

      .owl-prev {
         right: 43px;
      }

      .owl-next {
         right: 0
      }
   }

   .art_item {
      padding-bottom: 15px;

      .content {
         margin-top: 0;

         .title_news {
            font: 400 px_to_rem(18px) $fontmain;
            height: 50px;
            overflow: hidden;
         }
      }
   }

   &.box_thuongmai3,
   &.box_thuongmai4 {
      .thumb_art {
         .thumb_5x3 {
            padding-bottom: 100%;

            img {
               max-width: 90%;
               max-height: 90%;
               position: absolute;
               top: 50%;
               left: 50%;
               transform: translate(-50%, -50%);
               -moz-transform: translate(-50%, -50%);
               -webkit-transform: translate(-50%, -50%);
               -moz-transform: translate(-50%, -50%);
               -ms-transform: translate(-50%, -50%);
               -o-transform: translate(-50%, -50%);
            }
         }
      }
   }
}

.box-tieudiem {
   padding: 0 15px;
   margin-bottom: 0;

   .art_item {
      margin-bottom: 45px !important;

      .thumb_art {
         background: #eee;
         padding: 13px;
         width: 100%;
         margin: 0;
         float: none;

         &:before {
            content: "";
            position: absolute;
            left: 13px;
            right: 13px;
            height: 3px;
            top: 10px;
            background-image: -moz-linear-gradient(left, #16d1f2 #9dfeaf);
            background-image: -webkit-gradient(linear, left top, right top, from(#16d1f2), to(#9dfeaf));
            background-image: -webkit-linear-gradient(left, #16d1f2, #9dfeaf);
            background-image: -o-linear-gradient(left, #16d1f2, #9dfeaf);
         }
      }

      .content {
         margin-top: 10px;

         .title_news {
            font: bold px_to_rem(20px) $fontmain;
         }
      }

      &:before {
         content: "";
         background: #eee;
         position: absolute;
         width: 150px;
         height: 7px;
         bottom: -18px;
      }

      &:nth-child(2) {
         .thumb_art {
            &:before {
               background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
               background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
               background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
               background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
            }
         }
      }

      &:nth-child(3) {
         .thumb_art {
            &:before {
               background-image: -moz-linear-gradient(left, #8419fd #57b2fb);
               background-image: -webkit-gradient(linear, left top, right top, from(#8419fd), to(#57b2fb));
               background-image: -webkit-linear-gradient(left, #8419fd, #57b2fb);
               background-image: -o-linear-gradient(left, #8419fd, #57b2fb);
            }
         }
      }

      &:last-child {
         margin-bottom: 20px;
      }
   }
}


//======================
// Royal Kid 
//======================

.box-royalkid {
   padding: 0;

   .title_box_category {
      margin-bottom: 0;

      h2 {
         font-size: 20px;
         background: rgb(24, 41, 52);
         line-height: 1.2;
         padding: 10px 15px;
         font-weight: 500;

         a {
            color: #f8fafb;
         }

      }
   }

   &__list {
      padding: 5px 15px;
   }

   .art_item.img {
      &::before {
         display: none;
      }

      margin-bottom: 10px;
      padding-bottom: 0px;

      .thumb_art {
         padding: 0;
      }

      .content .title_news {
         color: #182934;
         font-size: 24px;
         font-weight: 700;

         a {
            color: inherit;
         }
      }
   }

   .art_item {
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #eaeaea;

      .content .title_news {
         font-size: 18px;
         font-weight: bold;
      }
   }

   &__logo {
      text-align: right;
      margin-top: 5px;
      float: left;
      width: 100%;

      a {
         display: inline-block;

         &+a {
            margin-left: 12px;
         }
      }
   }
}

.ns_of2019 {
   padding: 0 15px;

   .link {
      display: block;
      background: url(images/graphics/bg_ns_2019.png);
      background-size: cover;
      height: 38px;
      position: relative;
      color: #F1BC47;
   }

   h2 {
      text-transform: uppercase;
      font-size: 13px;
      padding: 5px 0 0 28px;

      span {
         display: block;
         font-weight: 400;
         font-size: 10px;

      }
   }
}