.art_item {
  position: relative;
  float: left;
  width: 100%;
  margin-bottom: 15px;

  .thumb_art {
    position: relative;
    width: 130px;
    margin-right: 15px;

    .tag {
      position: absolute;
      bottom: 12px;
      left: 55px;
      padding: 8px 10px;
      color: #fff;
      background-image: -moz-linear-gradient(left, rgba(132, 25, 253, 0.8), rgba(87, 178, 251, 0.6));
      background-image: -webkit-gradient(linear, left top, right top, from(rgba(140, 2, 254, 0.8)), to(rgba(87, 178, 251, 0.6)));
      background-image: -webkit-linear-gradient(left, rgba(132, 25, 253, 0.8), rgba(87, 178, 251, 0.6));
      background-image: -o-linear-gradient(left, rgba(132, 25, 253, 0.8), rgba(87, 178, 251, 0.6));

      a {
        color: #fff
      }
    }

    .icon_thumb {
      background-image: -moz-linear-gradient(left, #0799ff #21d4fd);
      background-image: -webkit-gradient(linear, left top, right top, from(#0799ff), to(#21d4fd));
      background-image: -webkit-linear-gradient(left, #0799ff, #21d4fd);
      background-image: -o-linear-gradient(left, #0799ff, #21d4fd);
      position: absolute;
      bottom: 0;
      left: 0;
      width: 25px;
      height: 25px;
      line-height: 25px;
      text-align: center;

      .icon-ns {
        color: #fff;
        margin: 0;
      }
    }
  }

  .content {
    .title_news {
      font: bold px_to_rem(18px) $fontmain;
      line-height: 1.3;
    }

    .description {
      font: 400 px_to_rem(18px) arial;
      margin-top: 10px;
      line-height: 1.4;
    }

    .tag {
      font-size: 14px;
      font-family: arial;
      color: #999;
      margin-top: 5px;

      a {
        color: $colormain
      }
    }
  }

  &.inner_section {
    padding: 0 15px;
    margin-bottom: 30px;
    float: left;
    width: 100%;

    &:after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: 15px;
      width: calc(100% - 30px);
      height: 1px;
      background: #eaeaea;
    }
  }

  &:last-of-type {
    &:after {
      display: none;
    }
  }

  &.last {
    margin-bottom: 20px;

    &:after {
      display: none;
    }
  }
}


.no-images {
