/*========HEADER===========*/
.header{
  background: #fff;
  border-bottom: 1px solid #e7e7e7;  
  &.header_cloned{position: fixed; top: 0; left: 0; z-index: 999;
    box-shadow: 0 1px 3px rgba(80,80,80,0.2), 0 3px 5px rgba(80,80,80,0.15);
    @include cubic-transition($delay: 0, $duration: 250ms, $property: (all)); 
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
    &.nav-up{
      -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
      transform: translateY(-100%);
    }

  }
  &.header_cloned + .section_nav {
      margin-top: 45px;
  }
  .icon-ns{margin: 0;color: #333;width: 16px;height: 16px;}
  .btn_control_menu {
    display: block;
    position: absolute;
    left: 0;
    top: 0px;
    cursor: pointer;
    width: 48px;
    height: 47px;
    text-align: center;
    line-height: 44px !important;
  }  
  .img_logo{
    margin: 4px auto 0;
    width: 90px;
    display: block;    
  }
  .logo-be{
    width: 66px;
    position: absolute;
    top: 13px;
    right: 38px;
   }
  #auto_search_button{
    background: none;
    border: 0;
    position: absolute;
    right: 0;
    top: 0;
    height: 47px;
    width: 45px;
    outline: 0;
    .icon-ns{width:20px;height: 20px;}  
  }
  .block_search{
    border-top: 1px solid #dfdfdf;
    display: none;
  }
}
.block_search{
  #search{
    background: #fff;
    padding: 15px;
  }
/*  .search_all{display: block!important;border-bottom: 0!important;}   */
  button{
    cursor: pointer;
    transition-duration: 350ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1); 
    outline: none;       
  }
  .icon-ns{margin: 0;color: #333;width: 16px;height: 16px;}
  .warp{
    border: 1px solid #dfdfdf;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;    
  }
  input{
    float: none;
    border: 0;    
    color: #333;
    padding-left: 10px;
    font-size: 16px !important;
    height: 38px;
    background: #fff;
    width: 78%;
    width: calc(100% - 75px);
    outline: none;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;        
    &::-webkit-input-placeholder {
      color: #999;
    }
    &::-moz-placeholder {
      color: #999;
    }
    &:-ms-input-placeholder {
      color: #999;
    }
    &:-moz-placeholder {
      color: #999;
    }           
  }    
  .btn_reset{
    visibility: hidden;
    opacity: 0;      
    background: none;
    border: 0;
    width: 35px;
    height: 38px;
    line-height: 30px;
    float: right;
    font-size: 30px;
    color: #999;
    font-family: -webkit-body;
  }
  .btn_search{
    border: 1px solid #dfdfdf;
    background: #fff;
    width: 40px;
    height: 40px;
    float: right;
    margin: -1px -1px 0 0px;  
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    .icon-ns{color: #666;}
  } 
  &.focus{
    .btn_reset{visibility: visible;opacity: 1;}
    .btn_search{background:$colormain!important;
        .icon-ns{color: #fff!important;}
    }
  }  
}

/*========END HEADER===========*/

