/*======breadcrum==========*/
#breadcrum{
  background: #f0f0f0;height: 42px;position: relative;
  .home{
    .icon-ns{color: #999;width: 18px;height: 18px;vertical-align: top;}
  }
  .menu_bread {
    display: table-cell;
    vertical-align: middle;
  }
  .menu_bread{
    li{
      display: table-cell;position: relative;height: 42px;vertical-align: middle;
      a{
        font: bold px_to_rem(15px) $fontmain;
        height: 100%;
        display: block;
        padding: 0 10px;
        white-space: nowrap;
        position: relative;   
        line-height: 42px!important; 
        &:after{display: none;}              
      }
      .icon-ns{color: #666;margin:-2px -10px 0 10px;width: 11px;height: 11px;}
      &.home{
        a{line-height: 22px!important;vertical-align: middle;display: table-cell;}
        .icon-ns{margin: 0 0 0 5px;color: #999;width: 18px;height: 18px;}
      }
      &.first{
        a{color: $colormain}
      }
      &.start{
        a{color: $colormain;}
        .icon-ns{color: #666;margin: -2px 12px 0 -8px;width: 11px;height: 11px;}
      }
      &.active{
        a{color: $colormain;}
        .icon-ns{margin: -2px 12px 0 -8px;}
      }      
      &.level2{
        a{
          padding-left: 20px;
          &:before{
            content: "";
            background: #ccc;
            height: 15px;
            width: 1px;
            position: absolute;
            left: 5px;
            top: 12px;
          }
          &:after{display: none;}          
        }
      }
    }        
  }
  .hamber_icon_video{
    position: absolute;
    right: 0;
    top: 0;
    background: #dddddd;
    height: 42px;
    line-height: 39px;
    width: 42px;
    text-align: center;
    .icon-ns{
      margin: 0;
      color: #333;
      width: 16px;
      height: 16px;
    }
  }
}
.breadcrum_video{
    .menu_bread{
        display: block!important;
        overflow-x: scroll;
        overflow-y: hidden;
        -webkit-overflow-scrolling: touch;
        white-space: nowrap;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        li{
          &:last-child{padding-right:45px;}
        }      
    }
}
/*======End breadcrum======*/

