  will-change: transform;
  .btn_control_menu{
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
    width: 48px;
    height: 44px;
    text-align: center;
    line-height: 42px !important;
    .icon-ns{margin: 0;color: #333;width: 16px;height: 16px;}  
  }
